import React, {useEffect, useState} from "react";
import PageWrapper from "../UserPage/frame/PageWrapper";
import InputWithLabel from "../UserPage/frame/InputWithLabel";
import LinkButton from "../UserPage/frame/LinkButton";
import {useDispatch} from "react-redux";

const initialState = {
    title: '',
    content: ''
}

const UploadProductPage = () => {
    const dispatch = useDispatch();
    const [formState, setForm] = useState(initialState);
    const {title, content} = formState;

    const onChange = (event) => {
        setForm({
            ...formState,
            [event.target.name]: event.target.value,
        });
    }

    const onClick = () => {
        dispatch();
    }

    const onKeyPress = (event) => {
        if (event.key == "Enter")
            onClick();
    }


    console.log(formState);
    return (
        <>
            <PageWrapper info = {"Share Your Travel Plan"}>
                <InputWithLabel label="제목" name="title" placeholder="제목을 적어주세요" type="text"  value = {title}  onChange = {onChange} onKeyPress = {onKeyPress} />
                <InputWithLabel label="설명" name="content" placeholder="설명을 적어주세요" type="text" value = {content}  onChange = {onChange} onKeyPress = {onKeyPress} />

                <LinkButton content="등록" width = "100%" />
            </PageWrapper>
        </>
    );
}

export default UploadProductPage;